import { useState } from 'react';
import mockThreatData from '@/data/mockThreatData.json';
import { ThreatFeed as ThreatFeedType, ThreatType } from '@/types/threat';
import SummaryCards from './SummaryCards';
import FilterBar from './FilterBar';
import ThreatList from './ThreatList';

const ThreatFeed = () => {
  const [activeFilter, setActiveFilter] = useState<ThreatType | 'All'>('All');
  const threatData: ThreatFeedType = mockThreatData;

  const filteredThreats = activeFilter === 'All'
    ? threatData.threats
    : threatData.threats.filter(threat => threat.type === activeFilter);

  const sortedThreats = [...filteredThreats].sort((a, b) => {
    if (b.risk_score !== a.risk_score) {
      return b.risk_score - a.risk_score;
    }
    return new Date(b.timestamp).getTime() - new Date(a.timestamp).getTime();
  });

  return (
    <div className="space-y-6">
      <SummaryCards summary={threatData.summary} />
      <FilterBar activeFilter={activeFilter} setActiveFilter={setActiveFilter} />
      <ThreatList threats={sortedThreats} />
    </div>
  );
};

export default ThreatFeed;