import { Threat } from '@/types/threat';
import { formatTime, severity, typeMeta } from '@/lib/utils';
import { Bug, MailWarning, ShieldAlert } from 'lucide-react';

interface ThreatItemProps {
  threat: Threat;
}

const ThreatItem = ({ threat }: ThreatItemProps) => {
  const { label, badgeClass, barClass } = severity(threat.risk_score);
  const { icon, accentClass } = typeMeta(threat.type);

  const IconComponent = icon === 'ShieldAlert' ? ShieldAlert : icon === 'Bug' ? Bug : MailWarning;

  return (
    <div className="bg-white shadow rounded-lg p-4 hover:shadow-md transition-shadow duration-200">
      <div className="flex justify-between items-center">
        <h3 className="text-lg font-medium text-gray-900 truncate">{threat.details.subject}</h3>
        <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${badgeClass}`}>
          {label} {threat.risk_score}
        </span>
      </div>
      <div className="mt-2 text-sm text-gray-500">
        <p>Sender: {threat.details.sender}</p>
        <p>Time: {formatTime(threat.timestamp)}</p>
      </div>
      <div className="mt-2">
        <div className="flex items-center space-x-2">
          <IconComponent className={`h-4 w-4 ${accentClass}`} aria-label={threat.type} />
          <span className="text-sm font-medium text-gray-700">{threat.type}</span>
        </div>
      </div>
      <div className="mt-2 bg-gray-200 rounded-full h-2">
        <div className={`${barClass} h-2 rounded-full`} style={{ width: `${threat.risk_score}%` }}></div>
      </div>
      <button onClick={() => console.log(threat.id)} className="mt-4 text-blue-500 hover:text-blue-700">
        View Details
      </button>
    </div>
  );
};

export default ThreatItem;